const db = wx.cloud.database()
import {uniIcons} from '@dcloudio/uni-ui'
export default {
	components: {uniIcons},
	data() {
		return {
			list: []
		};
	},
	methods: {
		async getList() {
			try{
				let res = await db.collection('scores').orderBy('score', 'desc').get()
				console.log(res)
				this.list = res.data
			}catch(e){
				console.log(e)
			}
		}
	},
	onLoad() {
		this.getList()
	}
}
